<template>
    <div class="page-register">
        <form class="register" method="post" @submit.prevent="registerForm">
            <h1>Register</h1>
            <transition name="fade">
                <div class="error" v-show="showError">{{errorMsg}}</div>
            </transition>
            <label class="icon-user">
                <input type="text" class="name" v-model="name" minlength="2" placeholder="Name" required>
            </label>
            <label class="icon-envelop">
                <input type="email" class="email" v-model="email" placeholder="Email" required>
            </label>
            <label class="icon-calendar" :class="{educationError: eduCheck}">
                <input type="text" class="education_start_date" @change="educationCheck" v-model="educationStart" onfocus="(this.type='date')" placeholder="Education Start Date" required>
            </label>
            <label class="icon-calendar">
                <input type="text" class="education_end_date" @change="educationCheck" v-model="educationEnd" onfocus="(this.type='date')" placeholder="Education End Date" required>
            </label>
            <label class="icon-lock" :class="{passError: samePass}">
                <input type="password" class="password" @keyup="passwCheck" v-model="password" pattern="^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9]).{6,}$" oninvalid="this.setCustomValidity('Must contain minimum 6 characters and at least one uppercase, one lowercase and a number')" oninput="this.setCustomValidity('')" placeholder="Password" required>
            </label>
            <label class="icon-lock">
                <input type="password" class="password" @keyup="passwCheck" v-model="passwordConfirm" pattern="^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9]).{6,}$" oninvalid="this.setCustomValidity('Must contain minimum 6 characters and at least one uppercase, one lowercase and a number')" oninput="this.setCustomValidity('')" placeholder="Confirm Password" required>
            </label> 
            <input type="checkbox" id="terms" v-model="terms" required>
            <label class="checkbox-label" for="terms">
                I agree to the <a href="#">Terms and conditions</a>
            </label>
            <button :disabled="samePass || eduCheck" @submit="registerForm" type="submit">Get Started</button>
        </form>
    </div>
</template>

<script>
export default {
    data() {
        return {
            name: '',
            email: '',
            educationStart: '',
            educationEnd: '',
            password: '',
            passwordConfirm: '',
            errorMsg: '',
            showError: false,
            terms: false,
            samePass: false,
            eduCheck: false,
            url: 'https://test-api.updivision.work/api/register?',
        }
    },
    methods: {
        passwCheck: function() {
            if(this.password.length > 0 && this.passwordConfirm.length > 0) {
                 if(this.password.toLowerCase() === this.passwordConfirm.toLowerCase()) {
                    this.samePass = false;
                    this.showError = false;
                } else {
                    this.samePass = true;
                    this.showError = true;
                    this.errorMsg = "Please make sure both paswords match";
                }
            } else {
                this.samePass = false;
                this.showError = false;
            }
        },
        educationCheck: function() {
            if(this.educationStart.length > 0 && this.educationEnd.length > 0) {
                if(Date.parse(this.educationStart) < Date.parse(this.educationEnd)) {
                    this.eduCheck = false;
                    this.showError = false;
                } else {
                    this.eduCheck = true;
                    this.showError = true;
                    this.errorMsg = "Please make sure 'Education Start Date' has an earlier date selected than 'Education End Date'";
                }
            } else {
                this.eduCheck = false;
                this.showError = false;
            }
        },
         registerForm:function() {
            const params = `name=${this.name}&email=${this.email}&password=${this.password}&password_confirmation=${this.passwordConfirm}&education_start_date=${this.educationStart}&education_end_date=${this.educationEnd}&terms=${this.terms}`;
            let status = ''; 

            fetch(this.url + params, {
                method: 'POST',
                headers: {
                    'Accept': 'application/json',
                },
            }).then(response => { 
                status = response.status;

                response.json()
                .then( (data) => { 
                    if(status === 200) {
                        this.$store.commit('userKey', data.accessToken);
                        this.$router.push({name: 'Products'});
                    }

                    if(status === 422) {
                        this.errorMsg = data.errors[Object.keys(data.errors)[0]][0];
                        this.showError = true;
                    }
                })
            }).catch(error => { 
                this.errorMsg = error.message;
                this.showError = true;
            });
        }
    }
}
</script>

<style>
.page-register {
    box-shadow: inset 0 0 0 1000px rgba(175, 94, 196, 0.6);
}
.register {
    padding: 50px 30px 55px 40px;
}
.register > * {
    display: block;
}
.register h1 {
    text-align: center;
    font-size: 35px;
    font-weight: 700;
}
.register input {
    width: 100%;
}
.register input[type=checkbox] {
    height: 1px;
    width: 1px;
    transform: translateX(7px) translateY(40px);
}
.checkbox-label {
    margin-top: 15px;
}
.register button {
    background-color: #9c27b0;
    text-transform: uppercase;
    margin: 35px auto 0;
    font-size: 11px;
    padding: 15px 30px;
}
.passError input,
.passError + label input,
.educationError input,
.educationError + label input {
    border-color: #ff0000;
}

@media screen and (max-width: 768px) {
    .register {
        padding: 40px 20px 35px;
    }
}
</style>
